export default {
	onLoad() {
		uni.getSystemInfo({
			success: function(e) {
				console.log(e)
			}
		})
	},
	data() {
		return {
			screenHeight: this.screenHeight,
			statusBarHeight : this.statusBarHeight ,
			//账户信息
			account:{}
		}
	},
	computed: {
		style() {
			let style = `height:${this.screenHeight - this.statusBarHeight}px; `;
			return style;
		},

	},
	methods:{
		enter:function(){
			uni.navigateTo({
				url:'enter'
			})
		},
		//发送验证码
		fasongyanzhengma:function(){
			if(this.$jiaoyan(this.account.phone)){
				this.$https({url:'/api/oauth/sendSms/user-register',data:{phone:this.account.phone},dengl:true,success:function(res){
					if(res.data.data){
						uni.showToast({
							title:'发送成功'
						})
					}else{
						uni.showToast({
							title:res.data.message,
							icon:'none'
						})
					}
				}})
			}else{
				uni.showToast({
					title:'请输入正确的手机号',
					icon:'none'
				})
			}
		},
		//注册按钮
		zhuce:function(){
			if(!this.$jiaoyan(this.account.phone)){
				uni.showToast({
					title:'请输入正确的手机号',
					icon:'none'
				})
			}else if(!this.account.password){
				uni.showToast({
					title:'请输入密码',
					icon:'none'
				})
			}else if(!this.account.checkCode){
				uni.showToast({
					title:'请输入验证码',
					icon:'none'
				})
			}else{
				this.$https({url:'/api/oauth/register',data:this.account,dengl:true,method:'post',success:function(res){
					
				}})
			}
		}
	}
}
